  font-family: Cinzel, serif;
  font-size: 16px;
  font-weight: 400;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.menu {
  overflow: auto;
}

.menu__top {
  z-index: 1;
  position: sticky;
  top: 0;
}

